import { Injectable } from '@angular/core';
import { Todo } from './todo';

@Injectable()
export class TodoDataService {

  lastId: number = 0;
  todos: Todo[] = [];

  constructor() {
    //  
  }

  addTodo(todo: Todo): TodoDataService {
    if (!todo.id) {
      todo.id = Date.now();
    }
    this.todos.push(todo);
    return this;
  }

  removeTodo(target: Todo): TodoDataService {
    try {
      this.todos = this.todos.filter((todo) => todo.id !== target.id);
    } catch (err) {
      console.error(err);
    }
    return this;
  }

  updateTodo(target: Todo, values: Object = {}): Todo {
    try {
      target.update(values);
    } catch (err) {
      console.error(err);
    }
    return target;
  }

  getTodoById(id: number): Todo {
    return this.todos.filter((todo) => todo.id === id).pop();
  }

  toggleTodoComplete(todo: Todo): boolean {
    try {
      this.updateTodo(todo, { complete: !todo.complete });
      return true;
    } catch (err) {
      console.log(err);
      return false;
    }
  }

}



